<template>
    <div>
        <div data-v-b1b07890="" class="page">
            <div data-v-152ab910="" data-v-b1b07890="" class="sub-header">
                <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
                <div data-v-152ab910="" class="title">确认订单</div><!----></div>
            <div data-v-b1b07890="" class="main">
                <div data-v-b1b07890="" class="address-wrap" @click="$router.push('/address')">
                    <div data-v-b1b07890="" class="persion-info">
                        <span data-v-b1b07890="">收货人：{{name}}</span>
                        <span data-v-b1b07890="">{{cellphone}}</span>
                    </div>
                    <div data-v-b1b07890="" class="address">
                        <img src="data:image/png;base64,"
                             alt="收货地址">
                        <span>{{isArea}}</span>
                    </div>
                    <div data-v-b1b07890="" class="address-null" v-show="name?false:true">您的收货地址为空,点击添加收货地址</div>
                    <div data-v-b1b07890="" class="arrow"></div>
                    <div data-v-b1b07890="" class="address-border-wrap">
                        <div data-v-b1b07890="" class="trapezoid style1"></div>
                        <div data-v-b1b07890="" class="trapezoid style2"></div>
                        <div data-v-b1b07890="" class="trapezoid style1"></div>
                        <div data-v-b1b07890="" class="trapezoid style2"></div>
                        <div data-v-b1b07890="" class="trapezoid style1"></div>
                        <div data-v-b1b07890="" class="trapezoid style2"></div>
                        <div data-v-b1b07890="" class="trapezoid style1"></div>
                        <div data-v-b1b07890="" class="trapezoid style2"></div>
                        <div data-v-b1b07890="" class="trapezoid style1"></div>
                        <div data-v-b1b07890="" class="trapezoid style2"></div>
                    </div>
                </div>
                <div data-v-b1b07890="" class="goods-wrap">
                    <div data-v-b1b07890="" class="goods-list" v-for="(item,index) in newGoodsdatas" :key="index">
                        <div data-v-b1b07890="" class="image"><img data-v-b1b07890=""
                                                                   :src="item.img"
                                                                   :alt="item.title"></div>
                        <div data-v-b1b07890="" class="goods-param">
                            <div data-v-b1b07890="" class="title">{{item.title}}</div>
                            <div data-v-b1b07890="" class="attr">
                                <span v-for="(item2,index2) in item.attrs" :key="index2">{{item2.title}}：
                                     <template v-for="(item3,index3) in item2.param">{{item3.title}}</template>
                                </span>
                            </div>
                            <div data-v-b1b07890="" class="amount">{{item.amount}}</div>
                            <div data-v-b1b07890="" class="price">￥{{item.price}}</div>
                        </div>
                    </div>
                </div>
                <ul data-v-b1b07890="" class="total-wrap">
                    <li data-v-b1b07890="">商品总额</li>
                    <li data-v-b1b07890="">￥{{total}}</li>
                </ul>
                <ul data-v-b1b07890="" class="total-wrap">
                    <li data-v-b1b07890="">运费</li>
                    <li data-v-b1b07890="">￥{{freight}}</li>
                </ul>
            </div>
            <div data-v-b1b07890="" class="balance-wrap">
                <div data-v-b1b07890="" class="price-wrap"><span data-v-b1b07890="">实际金额：</span><span
                        data-v-b1b07890="">￥{{total+freight}}</span></div>
                <div data-v-b1b07890="" class="balance-btn" @click="onSumit">提交订单</div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState, mapActions, mapGetters} from "vuex"

    export default {
        data() {
            return {
                aid: sessionStorage['aid'],
                name: "",
                cellphone: "",
                isArea: ""
            }
        },
        created() {
            //安全认证
            this.$utils.safeUser(this);
            //13717628481
            if (this.aid) {
                //获取地址详情信息
                this.getAddressInfo({
                    aid: this.aid, success: (res) => {

                        this.name = res.data.name;
                        this.cellphone = res.data.cellphone;
                        this.isArea = res.data.province + "" + res.data.city + "" + res.data.area + "" + res.data.address


                    }
                });
            } else {
                //获取默认地址
                this.getDefaultAddress({
                    success: (res) => {

                        sessionStorage['aid'] = res.data.aid;
                        this.name = res.data.name;
                        this.cellphone = res.data.cellphone;
                        this.isArea = res.data.province + "" + res.data.city + "" + res.data.area + "" + res.data.address


                    }
                });
            }


        },
        mounted() {
            document.title = this.$route.meta.title
        },
        methods: {
            ...mapActions({
                //获取收货地址信息
                getAddressInfo: "address/getAddressInfo",
                //获取默认地址
                getDefaultAddress: "address/getDefaultAddress",
                //提交订单
                getOrder: "order/getOrder"
            }),
            //提交订单
            async onSumit() {
                if (this.total > 0) {
                    let data = await this.getOrder({
                        freight: this.freight,
                        addsid: this.aid,
                        goodsData: JSON.stringify(this.goodsDatas)
                    })
                    if (data.code === 200) {
                        this.$router.push('/order/end');
                    }
                }
            }
        },
        computed: {
            ...mapState({
                //购物车数据
                goodsDatas: state => state.cart.goodsDatas
            }),

            ...mapGetters({
                //总价格
                total: "cart/total",
                //运费
                freight: "cart/freight"
            }),
            //选中能放在购物里面
            newGoodsdatas() {
                if (this.goodsDatas.length > 0) {
                    let data = this.goodsDatas.filter(item => {
                        return item.checked
                    })
                    return data;
                } else {
                    return [];
                }
            }
        }
    }
</script>

<style scoped>
