        width: 100%;
        min-height: 100vh;
        background-color: #ffffff;
        overflow: hidden;
    }

    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .main {
        width: 100%;
        margin-top: 1.4rem;
        margin-bottom: 1.4rem;
    }

    .address-wrap {
        width: 100%;
        height: 1.9rem;
        position: relative;
        z-index: 1;
    }

    .address-wrap .address-border-wrap {
        width: 100%;
        position: absolute;
        z-index: 1;
        left: 0;
        bottom: 0;
        display: flex;
        display: -webkit-flex;
    }

    .address-wrap .address-border-wrap .trapezoid {
        width: 6%;
        margin-left: 1%;
        margin-right: 1%;
    }

    .address-wrap .address-border-wrap .trapezoid.style1 {
        border-bottom: 0.08rem solid #fdcd4d;
        border-left: 0.08rem solid transparent;
        border-right: 0.08rem solid transparent;
    }

    .address-wrap .address-border-wrap .trapezoid.style2 {
        border-top: 0.08rem solid #84614d;
