    }

    .balance-wrap .balance-btn {
        flex: 1;
        -webkit-flex: 1;
        background-color: #CC0004;
        font-size: 0.32rem;
        color: #FFFFFF;
        line-height: 1rem;
        text-align: center;
    }
</style>